import React from 'react';

const Detail = ({ ERROR }) => {
    const input = [
        { label: "Tn phim", name: "filmName", id: "filmName", placeholder: "Tn phim", type: "text" },
        { label: "o din", name: "director", id: "director", placeholder: "o din", type: "text" },
        { label: "Din vin", name: "actor", id: "actor", placeholder: "Din vin", type: "text" },
        { label: "Th loi", name: "category", id: "category", placeholder: "Th loi", type: "text" },
        { label: "khi chiu", name: "premiere", id: "premiere", placeholder: "khi chiu", type: "date" },
        { label: "Thi lng", name: "time", id: "time", placeholder: "Thi lng", type: "number" },
        { label: "Ngn ng", name: "language", id: "language", placeholder: "Ngn ng", type: "text" },
        // { label: "Rated", name: "rate", id: "rate", placeholder: "Rated", type: "text" },
    ]


    return (
        <div className="col-8">

            <div className="border border-main rounded-lg h-100 p-2 bg-sub">
                {input.map((input, index) => (
                    <div className="form-group" key={index}>
                        <input type={input.type} name={input.name} id={input.id} className="form-control form-control-sm bg-main border-main text-light" placeholder={input.placeholder} data-error={input.placeholder} />
                        <small className="text-danger">{ERROR[input.id]}</small>
                    </div>
                ))}
                <div className="form-group">
                    <select className="form-control form-control-sm bg-main border-main text-light" name="rate" id="rate" data-error="rate">
                        <option value="">Chn mc  kim duyt</option>
                        <option value="P">P</option>
                        <option value="C13">C13</option>
                        <option value="C16">C16</option>
                        <option value="C18">C18</option>
                    </select>
                    <small className="text-danger">{ERROR["rate"]}</small>
                </div>
            </div>
        </div>
    )
}

export default Detail
